<template>
  <div class="max-w-2xl mx-auto p-6 bg-white rounded-lg shadow-md">
    <h2 class="text-2xl font-bold mb-6 text-gray-800">
      {{ mode === 'create' ? 'Registro de Cliente' : 'Editar Cliente' }}
    </h2>

    <!-- Mensajes de éxito y error -->
    <div v-if="success_message" class="mb-4 p-3 bg-green-100 border border-green-400 text-green-700 rounded">
      {{ success_message_text }}
    </div>
    
    <div v-if="error_message" class="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
      {{ error_message_text }}
    </div>

    <form @submit="submitForm" class="space-y-4">
      <!-- Campos en filas de dos columnas -->
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
        <!-- First Name -->
        <div>
          <label for="first_name" class="block text-sm font-medium text-gray-700 mb-1">
            Nombre *
          </label>
          <input
            id="first_name"
            v-model="formData.first_name"
            type="text"
            required
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder="Ingrese el nombre"
            disabled
          />
        </div>

        <!-- Last Name -->
        <div>
          <label for="last_name" class="block text-sm font-medium text-gray-700 mb-1">
            Apellido *
          </label>
          <input
            id="last_name"
            v-model="formData.last_name"
            type="text"
            required
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder="Ingrese el apellido"
            disabled
          />
        </div>
      </div>

      <!-- Email -->
      <div>
        <label for="email" class="block text-sm font-medium text-gray-700 mb-1">
          Email *
        </label>
        <input
          id="email"
          v-model="formData.email"
          type="email"
          required
          class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          placeholder="correo@ejemplo.com"
          disabled
        />
      </div>

      <!-- Address -->
      <div>
        <label for="address" class="block text-sm font-medium text-gray-700 mb-1">
          Dirección
        </label>
        <textarea
          id="address"
          v-model="formData.address"
          rows="3"
          class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
          placeholder="Ingrese la dirección completa"
          disabled
        ></textarea>
      </div>

      <!-- Phone y Zipcode en la misma fila -->
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
        <!-- Phone -->
        <div>
          <label for="phone" class="block text-sm font-medium text-gray-700 mb-1">
            Teléfono
          </label>
          <input
            id="phone"
            v-model="formData.phone"
            type="tel"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder="Ingrese el número de teléfono"
            disabled
          />
        </div>

        <!-- Zipcode -->
        <div>
          <label for="zipcode" class="block text-sm font-medium text-gray-700 mb-1">
            Código Postal
          </label>
          <input
            id="zipcode"
            v-model="formData.zipcode"
            type="number"
            min="0"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            placeholder="Ej: 28001"
            @keydown="preventNegative"
            disabled
          />
        </div>
      </div>

      <!-- Campos de contraseña (solo para crear o cuando se quiera cambiar) -->
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
        <!-- Password -->
        <div>
          <label for="password" class="block text-sm font-medium text-gray-700 mb-1">
            {{ mode === 'create' ? 'Contraseña *' : 'Nueva Contraseña' }}
          </label>
          <input
            id="password"
            v-model="formData.password"
            type="password"
            :required="mode === 'create'"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            :placeholder="mode === 'create' ? 'Ingrese la contraseña' : 'Dejar vacío para mantener la actual'"
            disabled
          />
        </div>

        <!-- Confirm Password -->
        <div>
          <label for="repassword" class="block text-sm font-medium text-gray-700 mb-1">
            {{ mode === 'create' ? 'Confirmar Contraseña *' : 'Confirmar Nueva Contraseña' }}
          </label>
          <input
            id="repassword"
            v-model="formData.repassword"
            type="password"
            :required="mode === 'create'"
            class="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
            :placeholder="mode === 'create' ? 'Confirme la contraseña' : 'Confirme la nueva contraseña'"
            disabled
          />
        </div>
      </div>

      <!-- Nota sobre la contraseña en modo edición -->
      <div v-if="mode === 'edit'" class="text-sm text-gray-600 bg-blue-50 p-3 rounded">
        <p>💡 <strong>Nota:</strong> Solo complete los campos de contraseña si desea cambiarla. De lo contrario, déjelos vacíos para mantener la contraseña actual.</p>
      </div>

      <!-- Botones de acción -->
      <!-- <div class="flex justify-end space-x-3 pt-4 border-t border-gray-200">
        <button
          type="button"
          @click="cancelForm"
          class="px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
        >
          Cancelar
        </button>
     
        <button
          type="submit"
          :disabled="load"
          class="px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          <span v-if="load" class="flex items-center">
            <svg class="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
              <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            Procesando...
          </span>
          <span v-else>
            {{ mode === 'create' ? 'Crear Cliente' : 'Actualizar Cliente' }}
          </span>
        </button>        
      </div> -->

    </form>
  </div>
</template>

<script>
import MarketplaceDeliveryService from "@/services/marketplace/marketplace_client.service";
import { EncryptionService } from "@/services/marketplace/marketplace_encryption.service";

export default {
  components: {},
  props: {
    info: {},
    mode: {
      default: 'create'
    }
  },
  name: "CrudMarketplaceClient",
  data() {
    return {
      load: false,
      error_message: false,
      error_message_text: '',
      success_message: false,
      success_message_text: '',

      formData: {
        id: null,
        first_name: '',
        last_name: '',
        email: '',
        address: '',
        phone: '',
        zipcode: '',
        password: '',
        repassword: '',
      }
    }
  },
  computed: {
    // Computed para obtener el nombre de la provincia seleccionada
  },
  created() {
    this.initForm();
  },
  watch: {
    mode() {
      this.initForm();
    }
  },
  methods: {
    initForm() {            
      this.load = true;
      
      if (this.mode === 'create') {
        // Inicializar formulario vacío para crear              
        this.formData = {                    
          id: null,
          first_name: '',
          last_name: '',
          email: '',
          address: '',
          phone: '',
          zipcode: '',
          password: '',
          repassword: '',
        };
        this.load = false;
      } else {
        // Cargar datos existentes para editar
        MarketplaceDeliveryService.getMarketplaceDelivery(this.info)
          .then(item => {   
            console.log('Datos cargados para editar:', item); // Debug
            this.formData = {
              id: item.id || null,
              first_name: item.first_name || '',
              last_name: item.last_name || '',
              email: item.email || '',
              address: item.address || '',
              phone: item.phone || '',
              zipcode: item.zipcode || '',
              password: '',
              repassword: '',
            };
          })
          .catch(error => {
            console.error('Error loading delivery config:', error);
            this.error_message = true;
            this.error_message_text = 'Error al cargar la configuración de entrega';
          }).finally(() => { 
            this.load = false;
          });
      }
    },

    async submitForm(e) {            
      e.preventDefault();
      this.error_message = false;
      this.success_message = false;
      
      // Validaciones básicas
      if (this.mode === 'create' && !this.formData.password) {
        this.error_message = true;
        this.error_message_text = 'La contraseña es requerida para crear un usuario';
        return;
      }
      
      if (this.formData.password && this.formData.password !== this.formData.repassword) {
        this.error_message = true;
        this.error_message_text = 'Las contraseñas no coinciden';
        return;
      }
      
      if (this.formData.password && this.formData.password.length < 6) {
        this.error_message = true;
        this.error_message_text = 'La contraseña debe tener al menos 6 caracteres';
        return;
      }

      if (this.formData.zipcode && !/^\d+$/.test(this.formData.zipcode.toString())) {
        this.error_message = true;
        this.error_message_text = 'El código postal debe contener solo números';
        return;
      }      
      
      this.load = true;
      
      const submitData = {        
        first_name: this.formData.first_name,
        last_name: this.formData.last_name,
        email: this.formData.email,
        address: this.formData.address,
        phone: this.formData.phone,
        zipcode: this.formData.zipcode || null,
      };

      if (this.mode !== 'create') submitData.id = this.formData.id

      const publicKey = sessionStorage.getItem("publicKey")

      if (this.formData.password && publicKey) {        
        submitData.password = await EncryptionService.encryptForBackend(this.formData.password,publicKey);        
      }
            
      const submitPromise = this.mode === 'create' 
        ? MarketplaceDeliveryService.addUpdMarketplaceClient(submitData)
        : MarketplaceDeliveryService.addUpdMarketplaceClient(submitData, false);
      
      submitPromise
        .then(response => {
          this.success_message = true;
          this.success_message_text = this.mode === 'create' 
            ? 'Cliente creado correctamente' 
            : 'Cliente actualizado correctamente';
          setTimeout(() => {
            this.success_message = false;
            this.$emit('close');
          }, 2000);
        })
        .catch(error => {
          this.error_message = true;
          this.error_message_text = this.mode === 'create' 
            ? 'Error al crear el cliente' 
            : 'Error al actualizar el cliente';
          console.error('Error submitting form:', error);
          setTimeout(() => {
            this.error_message = false;
          }, 3000);
        })
        .finally(() => {
          this.load = false;
        });
    },        
    
    cancelForm() {
      this.$emit('close');
    },
  }
}
</script>