<template >
    <div>        
        <div class="flex my-3 px-3 gap-3 border-double border border-gray-400 rounded-lg">                                                                               
            <div class="flex flex-col gap-3 my-3 mx-2 w-2/4">                
                <label class="uppercase block text-sm font-medium text-gray-700">Fichero Imagen </label>                               
                <div>
                    <input type="file" ref="file" @change="imageSelected" accept="image/*" class="">                    
                </div>   
                <div class="flex flex-col gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Subcategoria</label>
                    <input type="text" 
                    v-model="info.subcategory"
                    :readonly="(info.subcategory)"
                    class="flex-1 border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm w-2/3">            
                </div>                            
            </div>            
            <!-- Columna de datos fijos -->
            <div class="flex flex-col gap-2 my-3 mx-2 w-2/4">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-100">nombre de imagen Original: <b>{{ info.image_keyword }}</b> </label>                
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Tipo de fichero: <b>{{ info.ext }}</b> </label>
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Año: <b>{{ info.anno }}</b> </label>                
                <div class="flex gap-3">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Orden</label>
                    <input type="text" 
                    v-model="info.order"
                    class="border mt-1 block py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm w-1/3">            
                </div>   
            </div>
            <!-- Imagen Original -->
            <div class="img-container flex items-center gap-3 my-3 ml-auto mr-4" v-if="info.url">
                <figure class="relative rounded-lg" v-if="image_loaded">
                    <img :src="info.url" class="rounded-lg" :alt="info.image_keyword">                    
                    <span class="absolute image-delete-pos text-red cursor-pointer text-2xl" @click="clearImage"><i class="fas fa-times-circle"></i></span>
                </figure> 
            </div>
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">            
            <div v-if="error_message" class="error-message">
                <i class="fa fa-times-circle"></i>
                <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                </div>
                <div v-if="success_message" class="success-message">
                <i class="fa fa-check-circle"></i>
                <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
            </div>          
        </div> 
    </div>
</template>
<script>
import moment from 'moment'

export default {
    name: "AddImages",
    props: {
        info: {},
        index: 0
    },    
    data: () => ({  
        success_message_text: 'Se salvaron los datos correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,
        image_loaded: false,  
        load: false,                                  
    }),
    watch: {
        className(item) {
            this.info.class_name = item            
            this.$emit('imgStatus', {index: this.index, img: this.info})
        }
    },    
    created() {        
        this.image_loaded = (this.info)
    },  
    methods: {                      
        imageSelected(e) {            
            const file = e.target.files[0];
            this.info.url = URL.createObjectURL(file);
            this.image_loaded = true
            
            this.info.image_keyword = file.name.split('.')[0]
            this.info.ext = file.name.split('.')[1]
            this.info.file = file
            this.$emit('imgStatus', {index: this.index, img: this.info})
        }, 
        clearImage(){
            this.info.url = ''
            this.info.image_keyword = ''
            this.info.ext = ''
            this.info.file = ''
            this.image_loaded = false
            this.$refs.file.value = ''
            this.$emit('imgStatus', {index: this.index, img: this.info})
        },                                
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
    }
}
</script>
<style scoped>
    .img-container{
        width: 200px;
    }
    .image-delete-pos{
        top: -15px;
        right: -17px;
    }
</style>